<template>
  <div class="container">
    <div class="row">
      <button
        type="button"
        class="btn btn-outline-primary col"
        :class="{ active: selection == 0 }"
        @click="selection = 0"
      >
        
      </button>
      <button
        type="button"
        class="btn btn-outline-primary col"
        @click="selection = 1"
        :class="{ active: selection == 1 }"
      >
        
      </button>
      <button
        type="button"
        class="btn btn-outline-primary col"
        @click="selection = 2"
        :class="{ active: selection == 2 }"
      >
        
      </button>
      <button
        type="button"
        class="btn btn-outline-primary col"
        @click="selection = 3"
        :class="{ active: selection == 3 }"
      >
        
      </button>
      <button
        type="button"
        class="btn btn-outline-primary col"
        @click="selection = 4"
        :class="{ active: selection == 4 }"
      >
        
      </button>
    </div>

    <transition
      name="button"
      mode="out-in"
      :css="false"
      @before-enter="beforEnter"
      @enter="enter"
      @leave="leave"
    >
      <img
        v-if="selection == 0"
        :src="src[0]"
        key="0"
        :style="{ width: auto, height: '350px' }"
      />
      <img
        v-else-if="selection == 1"
        :src="src[1]"
        key="1"
        :style="{ width: auto, height: '350px' }"
      />
      <img
        v-else-if="selection == 2"
        :src="src[2]"
        key="2"
        :style="{ width: auto, height: '350px' }"
      />
      <img
        v-else-if="selection == 3"
        :src="src[3]"
        key="3"
        :style="{ width: auto, height: '350px' }"
      />
      <img
        v-else-if="selection == 4"
        :src="src[4]"
        key="4"
        :style="{ width: auto, height: '350px' }"
      />
    </transition>
  </div>
</template>
<script>
import factory from "../assets/factory.png";
import BusinessPremises from "../assets/BusinessPremises.png";
import ConstructionSite from "../assets/ConstructionSite.png";
import AddressFacilities from "../assets/AddressFacilities.png";
import OtherAnnouncements from "../assets/OtherAnnouncements.png";
import Velocity from "velocity-animate";

export default {
  data() {
    return {
      src: [
        factory,
        BusinessPremises,
        ConstructionSite,
        AddressFacilities,
        OtherAnnouncements
      ],
      selection: 0
    };
  },
  methods: {
    beforEnter(el) {
      el.style.opacity = 0;
    },
    enter(el, done) {
      Velocity(
        el,
        { opacity: 1 },
        { duration: 1000, easing: "ease-out-in", complete: done } // now with spring physics
      );
    },
    leave(el, done) {
      Velocity(
        el,
        { opacity: 0 },
        { duration: 250, easing: "ease-out-in", complete: done } // now with spring physics
      );
    }
  }
};
</script>
<style lang="scss" scoped>
div > button:hover {
  background-color: lavender;
  color: black;
}
div > button {
  color: black;
  border-color: darkgray;
}
</style>
