<template>
  <div class="container">
    <Card />

    <div class="input-group">
      <span class="input-group-text"></span>
      <input
        type="text"
        class="form-control"
        placeholder=""
        v-model="dB1"
      />
    </div>
    <div class="input-group">
      <span class="input-group-text"></span>
      <input
        type="text"
        class="form-control"
        placeholder=""
        v-model="dB2"
      />
      <button type="button" class="btn btn-secondary" v-on:click="Calculation">
        Submit
      </button>
    </div>
    <div class="input-group-prepend"></div>
    <div class="alert alert-info text-left" role="alert">
      <strong>  :</strong> {{ value }}
    </div>
  </div>
</template>
<script>
import Card from "@/components/Card.vue";
export default {
  data() {
    return {
      value: null,
      dB1: "",
      dB2: ""
    };
  },
  components: {
    Card
  },
  methods: {
    Calculation() {
      this.value =
        10 *
        (Math.log(Math.pow(10, 0.1 * this.dB1) + Math.pow(10, 0.1 * this.dB2)) /
          Math.log(10));
    }
  }
};
</script>
